import styled, { css } from 'styled-components'
import { useNavigate } from "react-router-dom";

const Div = styled.div`
    width: auto;
    height: auto;
    font-size: 2rem;
    display: flex;
    align-items: center;
`;

const P = styled.p`
    display: inline-block;
    width: auto;
    height: auto;
    color : #AE69FF; 
    ${props => props.space && css`
        transform: rotate(30deg) scale(1.2);
    `}
`;



function Next(props) {
    const navigate = useNavigate();
    const { page } = props

    const handleClick = (page) => {
        if (page == 'news') {
            navigate('/about')
        } else if (page == 'about') {
            navigate('/news')
        }
    }
    
    return (
        // <Div onClick={(e) => handleClick(page)}>
        <Div>
            <P>Next Page</P>
        </Div>
    )
}

export default Next;